@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700i,900&display=swap");
@import url("https://fonts.googleapis.com/css?family=Amaranth&display=swap");
@media all and (max-width: 800px) {
  h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 3.1rem; }
  h2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 2.2rem;
    font-style: italic; }
  h3 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: normal; }
  h4 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.6rem;
    font-weight: normal; }
  .main {
    margin-top: 60px; }
    .main .title {
      display: none; }
  .project-board h2 {
    padding-top: 1rem; } }

h1 {
  font-family: "Amaranth", sans-serif;
  font-size: 2.2rem; }

h2 {
  font-family: "Amaranth", sans-serif;
  font-size: 1.8rem; }

h3 {
  font-family: "Amaranth", sans-serif;
  margin-top: 0px;
  font-size: 1.2rem; }

h4 {
  font-family: "Amaranth", sans-serif;
  font-size: 1rem; }

p {
  font-size: 16px;
  font-family: "Roboto", sans-serif; }

@media (max-width: 800px) {
  p {
    font-size: 13px; } }

.footer {
  margin: 0;
  background-color: #496674;
  position: fixed;
  bottom: -12px;
  height: 15px;
  z-index: 2000; }

body {
  margin: 0;
  padding: 0;
  border: none;
  background: #fdfdfd;
  color: #14141d;
  font-family: "Roboto", sans-serif; }

.main {
  margin-top: 70px; }

@media (max-width: 800px) {
  .main {
    margin-top: 60px; } }
