@media all and (max-width: 800px) {
  h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 3.1rem; }
  h2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 2.2rem;
    font-style: italic; }
  h3 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: normal; }
  h4 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.6rem;
    font-weight: normal; }
  .main {
    margin-top: 60px; }
    .main .title {
      display: none; }
  .project-board h2 {
    padding-top: 1rem; } }

h1 {
  font-family: "Amaranth", sans-serif;
  font-size: 2.2rem; }

h2 {
  font-family: "Amaranth", sans-serif;
  font-size: 1.8rem; }

h3 {
  font-family: "Amaranth", sans-serif;
  margin-top: 0px;
  font-size: 1.2rem; }

h4 {
  font-family: "Amaranth", sans-serif;
  font-size: 1rem; }

p {
  font-size: 16px;
  font-family: "Roboto", sans-serif; }

@media (max-width: 800px) {
  p {
    font-size: 13px; } }

.footer {
  margin: 0;
  background-color: #496674;
  position: fixed;
  bottom: -12px;
  height: 15px;
  z-index: 2000; }

button {
  background: none;
  border: none;
  color: none;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: 0;
  cursor: pointer; }

.tab {
  border-left: #eff4f9 3px solid;
  background-color: #f8f8f8;
  text-align: center;
  padding: 2px;
  border: none;
  width: 78%;
  outline: none; }

.btn {
  padding: 10px 10px 10px 10px; }

.fluid {
  width: 100%; }

.circular {
  border-radius: 25px; }

.red {
  border: 1px solid #b01612;
  background-color: #b01612;
  color: #b01612; }

.warning {
  border: 1px solid #954e14;
  background-color: #954e14;
  color: #954e14; }

.error {
  border: 1px solid #b01612;
  background-color: #b01612;
  color: #b01612; }

.info {
  border: 1px solid #004c61;
  background-color: #004c61;
  color: #004c61; }

.blue {
  border: 1px solid #286089;
  background-color: #286089;
  color: #286089; }

.rounded {
  background: #eff4f9; }

.background {
  border: none;
  color: #eff4f9; }
