button {
  background: none;
  border: none;
  color: none;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: 0;
  cursor: pointer; }

.tab, .menu .menuRight * button {
  border-left: #eff4f9 3px solid;
  background-color: #f8f8f8;
  text-align: center;
  padding: 2px;
  border: none;
  width: 78%;
  outline: none; }

.btn {
  padding: 10px 10px 10px 10px; }

.fluid {
  width: 100%; }

.circular {
  border-radius: 25px; }

.red {
  border: 1px solid #b01612;
  background-color: #b01612;
  color: #b01612; }

.warning {
  border: 1px solid #954e14;
  background-color: #954e14;
  color: #954e14; }

.error {
  border: 1px solid #b01612;
  background-color: #b01612;
  color: #b01612; }

.info {
  border: 1px solid #004c61;
  background-color: #004c61;
  color: #004c61; }

.blue {
  border: 1px solid #286089;
  background-color: #286089;
  color: #286089; }

.rounded {
  background: #eff4f9; }

.background {
  border: none;
  color: #eff4f9; }

/* Navigation */
@media (max-width: 800px) {
  .navbar {
    display: none; } }

.navbar {
  background-color: #f18787e5;
  left: 0;
  right: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  z-index: 100;
  background-color: #f8f8f8; }

.menu {
  display: flex;
  flex-wrap: wrap; }
  .menu * {
    flex: 1 0; }
  .menu .menuLeft {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center; }
    .menu .menuLeft * {
      margin: 0;
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: auto; }
    .menu .menuLeft h1 {
      color: #004c61; }
    .menu .menuLeft button svg {
      padding-top: 8px;
      margin-right: 8px;
      width: 30px;
      height: 30px; }
  .menu .menuRight {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row-reverse;
    align-content: center;
    height: 60px; }
    .menu .menuRight * {
      flex: 0 1 100px; }
    .menu .menuRight * {
      height: 100%; }
