@media all and (max-width: 800px) {
  h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 3.1rem; }
  h2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 2.2rem;
    font-style: italic; }
  h3 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: normal; }
  h4 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.6rem;
    font-weight: normal; }
  .main {
    margin-top: 60px; }
    .main .title {
      display: none; }
  .project-board h2 {
    padding-top: 1rem; } }

h1 {
  font-family: "Amaranth", sans-serif;
  font-size: 2.2rem; }

h2 {
  font-family: "Amaranth", sans-serif;
  font-size: 1.8rem; }

h3 {
  font-family: "Amaranth", sans-serif;
  margin-top: 0px;
  font-size: 1.2rem; }

h4 {
  font-family: "Amaranth", sans-serif;
  font-size: 1rem; }

p {
  font-size: 16px;
  font-family: "Roboto", sans-serif; }

@media (max-width: 800px) {
  p {
    font-size: 13px; } }

.footer {
  margin: 0;
  background-color: #496674;
  position: fixed;
  bottom: -12px;
  height: 15px;
  z-index: 2000; }

* {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff; }

.app {
  margin: 2rem; }

a {
  color: #08c; }

code {
  background: #eee;
  padding: 0.1rem;
  font-family: "Menlo";
  font-size: 13px;
  color: #ff00aa; }

input {
  padding: 0.5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  border-radius: 4px;
  border: 1px solid #ccc; }

input:focus {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none; }

input.error {
  border-color: #b01612;
  background-color: white; }

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem; }

.inputFeedback {
  color: red;
  margin-top: 0.25rem; }

button {
  max-width: 150px;
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none; }

button:disabled {
  opacity: 0.5;
  cursor: not-allowed !important; }

button + button {
  margin-left: 0.5rem; }

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555; }
