@media all and (max-width: 800px) {
  h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 3.1rem; }
  h2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 2.2rem;
    font-style: italic; }
  h3 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: normal; }
  h4 {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.6rem;
    font-weight: normal; }
  .main {
    margin-top: 60px; }
    .main .title {
      display: none; }
  .project-board h2 {
    padding-top: 1rem; } }

h1 {
  font-family: "Amaranth", sans-serif;
  font-size: 2.2rem; }

h2 {
  font-family: "Amaranth", sans-serif;
  font-size: 1.8rem; }

h3 {
  font-family: "Amaranth", sans-serif;
  margin-top: 0px;
  font-size: 1.2rem; }

h4 {
  font-family: "Amaranth", sans-serif;
  font-size: 1rem; }

p {
  font-size: 16px;
  font-family: "Roboto", sans-serif; }

@media (max-width: 800px) {
  p {
    font-size: 13px; } }

.footer {
  margin: 0;
  background-color: #496674;
  position: fixed;
  bottom: -12px;
  height: 15px;
  z-index: 2000; }

/* Navigation */
@media (min-width: 801px) {
  .mobile {
    display: none; }
  .back {
    display: none; } }

@media (max-width: 800px) {
  .back {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 52px;
    background-color: #eaeff4;
    z-index: 3; }
    .back .title {
      margin-top: 13px;
      margin-left: 15px; }
      .back .title * {
        padding: 0;
        margin: 0;
        font-size: 1.3rem; }
  /* Position and sizing of burger button */
  .bm-burger-button {
    border: #db3966;
    position: fixed;
    width: 20px;
    height: 20px;
    right: 20px;
    top: 20px; }
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #286089; }
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #286089; }
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    display: none;
    height: 24px;
    width: 24px; }
  /* Color/shape of close button cross */
  .bm-cross {
    background: none; }
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%; }
  /* General sidebar styles */
  .bm-menu {
    background: #e0e0e0;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em; }
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47; }
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em; }
  .list-lang {
    outline: none; }
  /* Individual item */
  .bm-item {
    display: inline-block; }
  .fluid.button.bm-item {
    color: #e0e0e0;
    margin-bottom: 10px; }
  .circular.button.bm-item {
    color: #e0e0e0;
    margin-bottom: 10px; }
  .circular.button.bm-item.selected {
    background: #e0e0e0;
    border: #286089 1px solid;
    color: #286089; }
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3); } }
